<template>
  <div class="change-log container">
    <h1 class="text-center header"><i18n path="common.changeLog"></i18n></h1>
    <section v-for="(cl, index) in changeLogs" :key="index" class="mb-5">
      <h3 class="px-3 d-flex align-items-end">
        <span>v{{cl.version}}</span>
        <small class="ml-auto text-muted">{{cl.time}}</small>
      </h3>
      <hr class="mt-0 mb-4">
      <p v-for="(log, logIndex) in cl.changes" :key="logIndex" v-html="log"></p>
    </section>
  </div>
</template>

<script>
// import { getProjectByKey } from '@/store/urls'
import '@/styles/changelog.scss'

export default {
  data () {
    return {
      changeLogs: []
    }
  },
  methods: {
    loadChangeLog () {
      const s = this.$store.state; const project = this.$route.params.project
      if (project && s.language && s.changeLog) this.changeLogs = s.changeLog[project][s.language]
    }
  },
  watch: {
    '$store.state.language' () {
      this.loadChangeLog()
    }
  },
  beforeMount () {
    this.loadChangeLog()
  }
}
</script>
